.about-me-container {
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%);
  min-width: 600px;
}

.about-me-container h3 {
  text-align: center;
}



.experience-container {
  position: relative;
  top: 100%;
}

.carousel {
  margin-bottom: 10em;
  padding-bottom: 4em;
}

.carousel-item {
    padding-left: 20%;

}

.ui.card {
  min-width: 600px;
  padding-left: 20%;
}
.App {
  width: 100vw;
  height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.header-menu {
  float: right;
}

.App-intro {
  font-size: large;
}

h1.ui.header {
  font-size: 2rem;
}

h2.ui.header {
  font-size: 1.71428571rem;
}

h3.ui.header {
  font-size: 1.28571429rem;
}

h4.ui.header {
  font-size: 1.07142857rem; /* use as page header as well */
}

h5.ui.header {
  font-size: 1rem;
}

.ui.header {
  border: none;
  margin: calc(2rem - .14285714em) 0 1rem;
  padding: 0 0;
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 700;
  line-height: 1.28571429em;
  text-transform: none;
  color: rgba(0,0,0,.87);
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.name-container {
  width: 100%;
  position: absolute;
  top: 30%;
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
  text-align: center;
}

footer {
  position: fixed;
  bottom: 1em;
  left: 1em;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 100%;
  background-image: url(/static/media/blue-coupe-die-cast-model.69cfda92.jpg) !important;
  background-size: cover !important;
  height: 100vh;
  width: 100vw;
}


